.Option{
    text-decoration:none;
    font-size:90%;
    text-align: center;
    list-style:none;
    border: 1px solid #eee;
    -webkit-box-shadow: 0 2px 2px #cacaca;
            box-shadow: 0 2px 2px #cacaca;
    background-color: white;
    margin-top: 2px;
    margin-bottom:2px;
    width:50%;
    margin:auto;
    cursor: pointer;
    padding:8px;
}
.Option:hover{
    background-color: #eaeaea;
    -webkit-box-shadow: 0 2px 3px #cacaca;
            box-shadow: 0 2px 3px #cacaca;
}

hr{
border: 1px solid #eaeaea;
width:100%;
}



.OptionSelected{
    text-decoration:none;
    font-size:90%;
    text-align: center;
    list-style:none;
    -webkit-box-shadow: 0 2px 2px #ccc;
            box-shadow: 0 2px 2px #ccc;
    margin-top: 2px;
    margin-bottom:2px;
    width:50%;
    margin:auto;
    cursor: pointer;
    padding:8px;
    border: 1px solid #eee;
    color:white;
    background-color: #d50525;
}


