.Navbar {
background-color: #266abd;;
list-style: none;
display: inline-block ;
width:100%;
-webkit-box-sizing: border-box;
        box-sizing: border-box;
height:36px;
margin-top:0px;
-webkit-box-shadow: 0 0 5px 0 #cacaca;
        box-shadow: 0 0 5px 0 #cacaca;
height:40px;
padding:0;
}


.Link{
outline:none;
float:right;
color: white;
background-color: #266abd;
cursor: pointer;
text-decoration: none;
padding:10px;
border-radius: 1px;
margin-top:0px;
width:25%;
font-size:90%;
height:20px;
margin-bottom:0px;
}

.ActiveLink{
color:white;
background-color: #14427d; 
}

.Link:hover{
    background-color: #14509c;  
}


.NameLoggedUser{
float:left;
background-color: #266abd;
cursor: default;
padding:3px;
width:10%;
height:20px;
}

.ProfilePictureUser{
width:30px;
height:30px;
border-radius:50%;
vertical-align: middle;
margin:auto;
border: 2px solid white;
}




